
import { saveEmployeeLeaveRequest } from '@/services/EmployeeDataService';
import { useEmployeeLeaveStore } from '@/store/employee/leave';
import { defineComponent, reactive } from 'vue'
import { useRouter } from 'vue-router';
import * as toastr from 'toastr';

export default defineComponent({
    setup() {
        const EmLeave = useEmployeeLeaveStore();

        const router = useRouter();
        const form = reactive({
            type: null,
            pay_type: null,
            reason: null,
            from: null,
            to: null,
            time_from: null,
            time_to: null
        });

        const submitRequest = async () => {
            await EmLeave.addEmployeeLeaveRequest(form);
            await EmLeave.setComponentTo('leave-request-component');
            toastr.info('Leave added.');
        }


        return {
            EmLeave,
            form,
            submitRequest
        }
    },
})
