
import { useEmployeeLeaveStore } from '@/store/employee/leave'
import { computed, defineComponent } from 'vue'
import * as toastr from 'toastr';

export default defineComponent({
    async setup() {
        const EmLeave = useEmployeeLeaveStore();

        const form = computed(() => EmLeave.getSelectedRequest);

        const updateRequest = async () => {
            await EmLeave.updateEmployeeLeaveRequest(form.value, form.value?.id);
            await EmLeave.setComponentTo('leave-request-component');
            toastr.success('Request updated.');
        }
        
        return {
            EmLeave,
            form,
            updateRequest
        }
    },
})
